import { Component } from 'react';
import { connect } from 'react-redux';
import { fetchSurveys } from '../../actions/actionindex';
import { Link } from 'react-router-dom';

class SurveyList extends Component {

    componentDidMount() {
        this.props.fetchSurveys();
    }

    renderContent() {
        return this.props.surveys.reverse().map((survey, keys) => {
            return (
                <div key={keys} className="card">
                    <div className="card-body white-text">
                        <span className="card-title">{survey.title}</span>
                        <div>{survey.body}</div>
                        <div className='text-end'>Sent on: {new Date(survey.dateSent).toLocaleDateString()}</div>
                        <div className='text-end'>Last Response on: {survey.lastResponse ? new Date(survey.lastResponse).toLocaleDateString() : "none"}</div>
                        <Link to={`/surveyInfo/${survey._id}`} className='btn btn-outline-primary'>Show Response</Link>
                    </div>
                </div>
            );
        });
    }

    render() {
        return (
            <div className='container'>
                {this.renderContent()}
            </div>
        );
    }
}

function mapStateToProps(state) {
    return { surveys: state.surveys };
}

export default connect(mapStateToProps, { fetchSurveys })(SurveyList);